import React, {useState, useEffect} from 'react'
import Note from './components/Note'
import Notification from './components/Notification'
import Footer from './components/Footer'
import noteService from './services/notes'
import './index.css'

const App = () => {
  const [notes, setNotes] = useState([])
  const [newNote, setNewNote] = useState('a new note...')
  const [showAll, setShowAll] = useState(true)
  const [errorMsg, setErrorMsg] = useState('')

  useEffect(() =>
    noteService
      .getAll()
      .then(initialNotes => setNotes(initialNotes)),
    [])

  const addNote = (event) => {
    event.preventDefault()
    const noteObject = {
      content: newNote,
      date: new Date().toISOString(),
      important: Math.random() < 0.5,
    }

    noteService
      .create(noteObject)
      .then(returnedNote => {
        setNotes(notes.concat(returnedNote))
        setNewNote('')
      })
  }

  const toggleImportanceOf = id => () => {
    const note = notes.find(n => n.id === id)
    const toggledNote = {...note, important: !note.important}
    noteService
      .update(id, toggledNote)
      .then(returnedNote => setNotes(
        notes.map(n => n.id !== id ? n : returnedNote)))
      .catch(error => {
        setErrorMsg(`The note '${note.content}' was already deleted from server`)
        setTimeout(
          () => setErrorMsg(''),
          5000
        )
        setNotes(notes.filter(n => n.id !== id))
      })
  }

  const handleNoteChange = (event) => {
    console.log('event target:', event.target);
    setNewNote(event.target.value)
  }

  const notesToShow = showAll
    ? notes
    : notes.filter(note => note.important)

  return (
    <>
      <h1>Notes</h1>
      <Notification message={errorMsg} />
      <ul>
        {notesToShow.map(note =>
          <Note key={note.id} note={note} toggleImportance={toggleImportanceOf(note.id)} />  
        )}
      </ul>
      <form onSubmit={addNote}>
        <input value={newNote} onChange={handleNoteChange} />
        <button type="submit">save</button>
      </form>
      <button onClick={() => setShowAll(!showAll)}>show {showAll ? 'important' : 'all'} notes</button>
      <Footer />
    </>
  )
}


export default App